module.exports = {"fr":{"menu":{"map":"Carte","travels":"Voyages"},"map":{"back":"Retour","more":"Plus +"},"albums":{"peloponnese":{"displayName":"Pelopónnēsos","text":"Septembre 2020 : On embarque pour une nouvelle année d'aventure ! Première étape : Trois mois en Grèce dans le Péloponnèse... 🇬🇷<br> Le Péloponnèse ou Πελοπόννησος c'est cette grande péninsule au Sud-Est du continent. Entre mer, montagnes et télétravail, on partage notre temps à explorer les fonds marins, escalder les falaises voisines, explorer les montagnes du coin, se balader dans les mignons petits villages et dévorer la gastronomie locale !","story":{"1":"Lundi 21 Septembre, après plus de 1000 km sur l'autoroute italienne et 19h en ferry, nous voila enfin en Grèce ! Encore quelques heures de route et nous seront enfin dans notre nouveau chez nous !","2":"Une fois les valises déballées, première balade à Léonidio (Λεωνίδιο) et première visite du quartier...","4":"Notre petite maison au coeur de Leonidio, sous les falaises de Kokkinovrachos (Κόκκινος Βράχος) et à quelques kilomètres de la mer ! 🧗‍ 🌊","6":"Vue sur Leonidio, avec ces 3760 habitants la vie y est paisible ☺️ Le matin c'est l'église, et le coq qui nous sortent du lit ! 🐓","8":"Le monastère Elona à deux pas de Léonidio est perché sur une étroite avancée de la falaise.","10":"Le pope veille au grain dans les couloirs du monastère...","11":"Tous les soirs on enfourche les vélos et direction la plage de Plaka à 5km ! 🌊 Et on se découvre une nouvelle passion pour la plongée dans les grands fonds (au moins 4 mètres !) a observer les poissons","12":"Et c'est encore mieux quand les copains nous rejoignent pour découvrir les saveurs locales ! 😘 Sylvie & Matthieu !","13":"Premier week-end grèque ! Direction Monemvasia (Μονεμβάσια) dans le sud du Peloponnese. Au programme déambulation dans la ville et exploration des plages de Plitra (Πλύτρα).","15":"La ville fortifiée est magnifique ! Fondée au Vie siècle, elle a conservé un charme fou !","20":"Petit apéro depuis la plage et on a la chance de profiter d'un lever de Lune.","22":"Le week-end dernier on enfile les baskets pour une première rando greque ! Direction le Megali Tourla (Μεγάλη Τούρλα) le plu haut sommet du Parnon, il s'agit de la chaine de montagne visible depuis Leonidio. Ca grimpe tout de même à 1940m d'altitude !","23":"🤔 Heureusement que l'on a révisé notre alphabet grec avant de partir ;)","24":"On the top ! 🏔 Mer & Montagne ! La rando a pas l'air d'être le sport national dans le coin ! Les chemins sont très bien balisés mais on a pas croisé grand monde ^","25":"Et au retour un joli point de vue sur le Monastère de Panagia Malevi (Μονή Μαλεβής Αρκαδίας). Y a pas à dire ils ont toujours du style les monastères grecques !","26":"Romaric profite de la quasi absence totale de lumière et de nuage pour mitrailler les étoiles depuis ... la fenêtre de la chambre ! Séances photos étoilées depuis l'intérieur c'est quand même plutôt confort !","27":"Samedi dernier, direction le Magne (Μάνη) pour un road trip le long de la cote.","29":"Ici Pas de petites maisons blanches aux coupoles bleues surplombant une mer d’un bleu profond, pas d’hôtels de luxe mais des villages de pierre aux allures de forteresses, des paysages arides et sauvages, des troupeaux en liberté, et des routes au milieu de rien. Si vous aimez conduire sur des routes sinueuses, offrant des points de vue à couper le souffle, sans être embêté par le trafic, le Magne est définitivement fait pour vous !","31":"Tout au Sud de la pointe la plus au Sud du Péloponnèse : Tenaro (Φάρος Ταίναρο)","33":"Ces tours, véritables symboles de la région, représentent l’héritage d’une époque (le XVIème siècle) où les Maniotes, avaient besoin de se protéger des envahisseurs extérieurs.","35":"Sur la route direction Ermioni (Ἑρμιόνη), les champs d'oliviers à perte de vue...","37":"Après une courte traversée en bateau, on débarque à Hydra (Ύδρα). C’est la seule île grecque sur laquelle on ne trouve aucun véhicule à moteur (hormis le camion-poubelle). Ni voiture ni scooter !","38":"Première mission ? S'engoufrer dans les petites ruelles derrière le port, s'y perdre et croiser le millier de chat qui ronronnent le long des rues. Ce sont définitivement eux les vrais Pachas sur l'île !","41":"Le port d’Hydra est un savant mélange de des petites barques, de bateaux de pêcheurs, de gros yachts, de mulets qui patientent, et les chats qui attendent le retour des pêcheurs...","44":"On enfile les baskets, direction le Mont Eros le point culminant de l'île, pour un panoama à 360 degrés !","49":"Dernier coucher de soleil... Bye bye Hydra !","50":"Vendredi, on prend la route direction Sparte ! Au programme la visite de Mistra (Μυστράς) une ancienne cité fondée par les Francs au xiiie siècle. Elle est aujourd'hui en ruines !","51":"Moulte fois détruite et recontruite, seuls les monastères et les églises tiennent encore debout. Mais comptez quand même trois heures de visite, il y en a beaucoup, beaucoup, beaucoup ...","56":"Départ à pied de la maison pour une balade sur les hauteurs de Leonidio.","57":"On voulait découvrir l'endroit depuis quelques semaines déjà, et l'on a pas été déçu ! Une journée à Kyparissi (Κυπαρίσσι) petit village dans un écrin de verdure, avec des centaines de voies d'escalade à explorer ! On a adoré, et l'on reviendra ;)","58":"Sur le chemin du retour, petit arrêt à Fokiano, pour un café et un chocolat face à la mer.","59":"Dimanche dernier, on prend une nouvelle fois la route, pour une journée visite des sites antiques ! Première étape Mycènes (Μυκήνες), une cité antique préhellénique.","60":"La porte des Lionnes est l'entrée principale de la citadelle de Mycènes. Elle a été érigée au XIIIe siècle avant notre ère, vers 1250 av. J.-C. (!!!) et tient (toujours debout) son nom de la sculpture représentant deux lionnes placées au-dessus de la porte.","63":"A quelques kilomètres de Mycènes, nous découvrons le Théatre d'Epidaure ! Remarquablement conservé, il est dédié à l'ancier dieu grec de la médecine, Asclépios.","64":"C'est grandiose ! Le Théatre peut accueilir jusqu'à 12000 personnes, l'acoustique du théâtre est justement renommée. Le moindre son produit au bas des gradins se propage jusqu'aux rangées supérieures. On a testé c'est impressionnant !","65":"Le Sanctuaire d'Asclépios date du IVe siècle av. J.-C.. Durant l'Antiquité, les pèlerins accouraient de toute la Grèce pour se faire soigner dans le sanctuaire du dieu guérisseur.","68":"J-1 avant le confinement ! Ni une ni deux, on monte dans la voiture direction le Nord ! Premier arrêt L'Acrocorinthe (Ακροκόρινθος), une citadelle antique défendant l'isthme de Corinthe depuis le vie sicèle av. J.-C... La place était à priori stratégique, puisque occupée successivement par les Corinthiens, Romains, Byzantins, Francs, Vénitiens et Ottomans... Un joyeux mélange culturel que l'on visite encore une fois complètement seuls !","71":"Pour rejoindre le continent (et quitter pour la toute première fois le Péloponnèse... pour quelques heures seulement !), on passe par le mythique Canal de Corinthe (Διώρυγα της Κορίνθου) ! 6km de long, 24m de large, c'est impresionnant ! Dès le viie siècle av. J.-C., les Bacchiades avaient relié le golfe Saronique au golfe de Corinthe par un...chemin de bois pour le roulage des navires ! Il faudra attendre 1893 pour que les travaux entrepris 11 ans plus tot évitent enfin aux navires un détour de 400 km autour de la péninsule du Péloponnèse !","73":"Derniers stops avant de rentrer à la maison : Le 'lac de Vouliagmenis' (qui n'en est pas vraiment un ! Un petit canal permet en fait à l'eau de mer de rentrer dans le Golf !) et le site antique Heraion (Αρχαιολογικός Χώρος Ηραίου Περαχώρας)...","74":"...pour une dernière bouffée d'air frais et une balade sur les hauteurs !","75":"Confinement oblige, on reste dans le proche voisinage de la maison, heureusement la mer est accessible ! On s'y est même baigné ce weekend ! Pour un 15 novembre, elle était plutôt bonne !","76":"Sur le port, Romaric fait connaissance avec son nouveau copain chat.","77":"On profite de ces journées toujours aussi ensoleillées pour arpenter les chemins autour de Leonidio, et découvrir les spots d'eslcalade de fou ! Ici le secteur 'Mars' qui tient son nom de la couleur orangée du rocher et de la difficulté des voies réservées à quelques ... extraterrestres ! ","80":"Les champs d'orangers et de mandariniers ont pris de la couleur depuis quelques jours ! C'est beau et c'est bon !","81":"Fin du week-end par une belle grande voie au-dessus de Léonidio (encore !), le temps et le rochers sont splendides, on rentre en stop bercé par la musique traditionnelle grecque... Que demander de plus ? ;)","83":"Le confinement est toujours de mise et nous en profitons pour visiter les alentours de Leonidio pour découvrir de nouveaux points de vues : ici depuis la chapelle : Προφήτης Ηλίας (La bastille Grenobloise de Λεωνίδιο) :) !","85":"L'hiver arrive, ça se sent, la pluie tombe aussi dans le Péloponnèse, les oliviers ont enfin de l'eau aux pieds. :)","87":"La balade du jour nous promène sur les hauteurs. Objectif : Rejoindre la chapelle de Saint Athanasios. On profite de la vue imprenable sur la mer et la plage de Poulithra (Πούλιθρα).","91":"En fin de journée, on s'embarque dans un mini road trip, direction le tout petit village de Paleochori. Ici pas un chat, le village semble presque endormi... L'automne est maintenant bien installé ! Et les arbres prennent des couleurs ! ","92":"Photo prise depuis le haut d'une voie d'escalade dans un de nos endroits fétiches pour grimper !","93":"Rando un jour, rando toujours... Cette fois-ci pour tenter de rejoindre le village de Voskina (Βόσκινα).","94":"Ca y est il est l'heure de dire au revoir à notre maison adorée de Léonidio pour de nouvelles aventures :)","95":"On se met en mode hiver avec notre nouveau petit chez nous pour fêter Noël !","97":"On profite de la terasse pour admirer les couchers soleil tous les après-midi.","99":"Déouverte de la ville de Naupacte (Ναύπακτος) et de sa forteresse qui connu de nombreux assauts de la part des Italiens..","101":".. en finissant par la visite de son jolie petit port","106":"A l'ascension du mont Varasova (Βαράσοβα) qui est notre terrain jeu de grimpe depuis le début des vacances. Du haut de ses 917m, nous aurons au sommet le droit à une vue nuagueuse dans une ambiance “Noëlique”, hé oui on est le 23 décembre :)"}},"thessalia":{"displayName":"Epirus & Thessalia","text":"Après 3 mois dans le Peloponnèse on met les voiles vers les Nord direction l'Epire et la Thessalie ! On s'installe pendant presque deux mois à Kalambaka ou Καλαμπάκα au pied des Météores ! L'objectif ? Grimper évidemment ces énormes pitons rocheux sculptés par l'érosion et qui semblent tout droit tombés du ciel. Mais aussi randonner dans les parcs nationaux du coin : le Parc de Vikos – Aoos, et ses gorges impressionnantes et le Parc de Tzoumerka avec ces sommets enneigés. C'est parti pour l'aventure !","story":{"1":"Avant d'arriver à Kalambaka (Καλαμπάκα), on pose nos valises à Rodotopi (Ροδοτόπι) un petit village à proximité de Ioannina (Ιωάννινα) et de son lac surtout célèbre pour son île qui servit de refuge à Ali Pacha, lors du conflit qui opposa la région aux forces Ottomanes.","2":"Après 4 jours de déluge, le soleil pointe enfin le bout de son nez ! Ni une ni deux on monte dans la voiture direction les Gorges de Vikos au cœur du pays de Zagoria (qui signifie 'au-delà des montagnes'). Vikos est même classé dans le livre des records comme le canyon le plus profond du monde par rapport à sa largeur ! Et l'on ne va pas être déçu !","3":"On colle la paroi sur le petit chemin de 'promenade' qui serpente le long de la falaise ! Pas moins de 900m de vide s'étendent jusqu'à la rivière au fond du canyon.","9":"Héhé la neige grecque pointe le bout de son nez...","10":"Célèbres dans la région les petits ponts de pierre sont nombreux dans le coin. Ils datent du XVIIième siècle, et font jusqu'à 50m de long.","17":"Les balades et points de vue depuis le haut des falaises nous auront donné un avant goût de la grandeur des gorges ! Mais la seule façon d'y pénétrer et d'explorer le canyon se fait via le chemin de randonnée au fond qui suit la rivière. Le rendez-vous est pris pour ce printemps !","18":"Pour cette seconde journée de soleil (du jamais vu dans la région !) on roule direction le sud, et le parc national de Tzoumerka ! Au programme : des monts enneigés culminant à plus de 2000m et une pointe de nostalgie en pensant à nos montagnes grenobloises...","19":"Le long des routes grecques on aperçoit très souvent des petites maisonnettes avec une petite bougie ou des icônes à l'intérieur. Appelées 'afiéroma', il s'agit en fait de petites chapelles qui témoignent de la mémoire des défunts ou sont érigées en remerciement d'un décès évité lors d'un accident. Sympa !","22":"On est encore une fois tout seul le long des chemins de rando (à cause du confinement général toujours en vigueur en Grèce peut-être ?!), et les paysages sont incroyables ! Qui croirait qu'il s'agit de la Grèce ?!","24":"Avant de quitter le parc, un dernier arrêt au monastère de Kipinas (Ιερά Μονή Κηπίναςn) qui semble fusionner avec la roche... Malheureusement pas possible de le visiter en ce moment.","25":"Nouvelle maison, nouvelle vue ! Welcome dans les Météores et plus précisément à Kalambaka (Καλαμπάκα) où nous avons trouvé racine jusqu'à fin février.","27":"Les Météores (Μετέωρα) sont une formation géologique du nord de la Grèce, en Thessalie, dans la vallée du Pénée. Ils abritent des monastères chrétiens orthodoxes (Μετέωρα Μοναστήρια, « monastères suspendus au ciel ») perchés au sommet de falaises et pitons rocheux gris, sculptés par l'érosion. Le site est inscrit au patrimoine mondial de l'UNESCO depuis 1988.","30":"Les monastères sont perchés dans les falaises détritiques, composées de « poudingue » (conglomérat formé de galets liés par un ciment sableux) qui surplombent au nord la petite ville de Kalambaka.","31":"On en profite pour attaquer notre première grande voie du coin, avec ce rocher particulier que nous n'avons jamais grimpé jusqu'ici. Les points sont espacés, mais la vue est grandiose il y a de l'ambiance tout en haut de l'Ambaria ! <3","32":"Tradition oblige, toutes les grandes voies du coin se termine par un petit carnet mis à disposition tout en haut afin que chacun puisse y écrire son petit mot en signe de passage, c'est chose faite pour nous aussi ! ;)","35":"Entre midi et deux, le soir après le boulot, ou le week-end, toutes les occasions sont bonnes pour découvrir les Météores ! Des dizaines de kilomètres de sentiers sont en plus balisés, et à pied sans personne c'est carrément grandiose !","36":"Ambiance féérique quand la neige recouvre les monastères !","41":"Pour accéder aux nombreux monastères, il faut grimper de nombreuses marches, ou alors... monter dans les mini téléphériques qui transportent personnes et ravitaillements !","46":"Ce dimanche on prend de la hauteur, et on grimpe jusqu'à Koromilia (Κορομηλιά). Ce petit village, perché au-dessu de Kalambaka (complètement désert en hiver) est le point de départ pour une jolie rando qui monte au sommet du même nom à 1650m. La neige tombée le week-ed dernier a bien tenue, et l'on profite de ces paysages d'hiver en plein coeur de la Grèce !","48":"Tout au fond les Météores, ils ont l'air tout petits vu d'ici !","50":"En route pour le Lac Plastiras, qu'on pourrait nommer 'La Suisse Grecque' ! L'accès est pour le moins scabreux, car les violents orages du coin ont eu des effets désastreux sur les routes de la vallée. On zig-zag donc entre les énormes affaissements de terrain et rochers pour atteindre un peu par hasard ce magnifique point de vue, que ce soit côté Est ou Ouest ;)","51":"Ca c'est le côté Est !","52":"L'été arrive et la grimpette se fait de plus en plus récurrente devant ce caillou merveilleux et bien sec !","54":"Le repos de la guerrière au sommet !","55":"Hé voilà qui on croise au détour d'un météore à bronzer au soleil :D","56":"Grosses chutes cette nuit, le balcon n'est plus utilisable.. mais la vue prend une tout autre forme !","57":"Allé on remonte au sommet pour admirer encore et toujours les Météores et ses Monastères, on ne s'en lasse pas.","58":"Dernière petite photo souvenir pour notre dernier jour, direction le Pelion !"}},"magnesia":{"displayName":"Magnesia","text":"Les 2 mois aux Météores ont été formidables, mais il était temps pour nous de quitter les montagnes du nord de la Grèce et de retrouver la mer et son eau bleue ! On s'intalle pour un petit mois dans le Pelion dans un petit village dénommé Agria (Αγριά), situé tout près de la grande ville de Volos (Βόλος). Pas de grimpe ici, mais de sublimes paysages et plages !","story":{"1":"Première soirée dans le Pelion et notre premier coucher de soleil depuis le balcon. On retrouve les palmiers, les ovliers et citronniers grecs !","2":"Sur les quais du port, les chiens (toujours aussi nombreux) se prélassent au soleil ... Le printemps est bien de retour ! ","3":"Premier week-end dans le Pélion, on s'embarque dans un petit road trip direction la pointe sud de la presqu'île. Les plages de galets, les petites criques et les ports de pêcheurs s'enchainent.","8":"Ce matin au programme : Randonnée sur les hauteurs du Pélion ! à 1600m la neige est bien là, et on voit la mer tout en bas !","9":"Au sommet surprise... Mélange de base militaire laissée à l'abandon et de d'antennes de diffusion, ambiance base secrète ... On se demande si on a vraiment le droit d'être ici ?","10":"Et l'après-midi, direction la plage pour notre première baignade de l'année ! L'eau est gelée ! Mais qu'est ce que ça fait du bien !"}}},"loader":{"0":"Egouttage de la feta","1":"Pressage de l'huile d'olive","2":"Préparation du saganaki","3":"Elaboration de la salade grecque","4":"Confection du tzatziki","5":"Cuisson de la moussaka","6":"Préparation d'un café frappé","7":"Dégustation de l'ouzo"}}}

